<template>
    <section>
        <section class="layout-waybill" v-if="hasWaybill">
            <div class="header">
                <p class="title">
                    #{{waybill.numberInvoice}}
                </p>

                <section class="edit-wrapper" v-if="waybill.isEditable">
                    <span v-show="!editing.isEditing" v-on:click="editHandler">редактировать</span>

                    <img v-show="editing.isEditing" class="icon" src="/svgs/check.svg" alt="сохранить изменения"/>
                    <img v-show="editing.isEditing" v-on:click="editHandler" class="icon"
                         src="/svgs/times.svg" alt="отменить изменения"/>
                </section>

                <p class="delivery-date" v-if="waybill.deliveryDate">{{waybill.deliveryDate}}</p>
                <p class="delivery-date" v-else>Резерв</p>
            </div>

            <div class="content">

                <ProductsTable  @quantityChange="onQuantityChange" @remove="onRemove"
                                v-bind:data="waybill.products" :has-numbering="true"
                                v-bind:is-editable="editing.isEditing">
                </ProductsTable>

                <MDRaisedButton v-if="editing.isEditing" text="Добавить Товар" v-on:click="onAddButtonClick" />

                <MDInput class="comment-min" label="Комментарий" v-if="editing.isEditing">
                </MDInput>
                <p class="comment" v-if="!editing.isEditing && waybill.comment">Примечание: {{waybill.comment}}</p>
                <p class="comment" v-if="!editing.isEditing && !waybill.comment">Нету примечания</p>

                <div class="sub-items">
                    <p class="is-paid is-paid-true" v-if="waybill.isPaid">Оплачен</p>
                    <p class="is-paid is-paid-false" v-if="!waybill.isPaid">Не оплачен</p>
                    <p class="total-price">Всего:
                        <span class="bold" v-if="!editing.isEditing">{{waybill.totalPrice}}₴</span>
                        <span class="bold" v-if="editing.isEditing">{{calculatedTotalPrice}}₴</span>
                    </p>
                </div>

            </div>

        </section>

        <section class="center">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </section>

        <p v-if="!hasWaybill && !isLoading" class="error">Накладная с номером: {{id}}. Не найдена</p>

        <Dialog ref="dialog" v-if="editing.isEditing"
                @hide="onDialogHide" @return="onDialogReturn"
                v-bind:can-return="editing.category.length !== 0 || editing.searchRequest.length !== 0">

            <section v-if="editing.category === '' && editing.searchRequest === ''">

                <section class="search-wrapper">
                    <div class="search">
                        <input placeholder="Поиск Товара" type="text" autocorrect="off" autocapitalize="off"
                               @input="onSearchInput" @keyup.enter="onSearchResultChoose($event.target.value)"
                               ref="searchInput"/>

                        <section class="search-helper" v-if="editing.searchHelperResults.length !== 0 && editing.category === ''">
                            <p v-for="item in editing.searchHelperResults" v-on:click="onSearchResultChoose(item)">{{item}}</p>
                        </section>
                    </div>
                </section>

                <CategoryChoose @choose="onCategoryChoose" @basketRemove="onRemove"
                                :is-component="true" v-bind:data="waybill.products"/>
            </section>

            <Catalog v-if="editing.category !== '' || editing.parentCategory !== '' || editing.searchRequest !== ''"
                     :is-component="true" v-bind:category="editing.category" v-bind:parent-category="editing.parentCategory"
                     v-bind:search-request-prop="editing.searchRequest" v-bind:data="waybill.products"
                     button-text="Добавить" @buy="onBuy" @basketRemove="onRemove"
            />
        </Dialog>
    </section>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex';
    import modules from '../store/all.modules.name';
    import actions from "../store/all.actions.type";

    import ProductsTable from "./components/ProductsTable";
    import MDRaisedButton from "./components/MD/MDRaisedButton";
    import MDInput from "./components/MD/MDInput";
    import LoadingSpinner from "./components/LoadingSpinner";
    import {CatalogAPI} from "../api/catalog"
    /*import CategoryChoose from './CategoryChoose';
    import Dialog from './components/Dialog';
    import Catalog from "./Catalog";*/

    export default {
        name: "Waybill",

        components: {
            MDRaisedButton,
            MDInput,
            LoadingSpinner,
            ProductsTable,
            CategoryChoose: () => import("./CategoryChoose"),
            Dialog: () => import('./components/Dialog'),
            Catalog: () => import('./Catalog') ,
        },

        data: () => {
            return {
                waybill: null,
                waybillCopy: null,

                editing: {
                    isEditing: false,
                    searchHelperResults: [],
                    category: "",
                    parentCategory: "",
                    searchRequest: "",
                }
            }
        },

        methods: {
            ...mapActions(modules.waybills, [
                actions.waybills.GET_EXTENDED_WAYBILL,
            ]),

            loadWaybill() {
                this.getExtendedWaybill(this.id).then(res => {
                    this.waybill = res;
                })
            },

            cancelAllChanges() {
                this.waybill = Object.assign({}, this.waybillCopy);
                this.waybillCopy = null;
            },

            editHandler() {
                this.editing.isEditing = !this.editing.isEditing;

                 if (this.editing.isEditing === false) {
                     this.cancelAllChanges();
                } else {
                     this.waybillCopy = JSON.parse(JSON.stringify(this.waybill));
                     this.waybill.products.forEach((item) => {
                         item.isValid = true;
                     })
                }
            },

            onBuy(item) {
                let res = this.waybill.products.find((el) => {
                    return el.id === item.id;
                });

                if (res) {
                    res.boughtQuantity++;
                } else {
                    let copy = JSON.parse(JSON.stringify(item));
                    copy.boughtQuantity = 1;
                    copy.isValid = true;
                    this.waybill.products.push(copy);
                }

            },

            onSearchInput(event) {
                let value = event.target.value;
                if (value.length !== 0) {
                    CatalogAPI.getSearchHelper(value).then((res) => {

                        if (res.status === 200) {
                            this.editing.searchHelperResults = res.data;
                        } else {
                            this.editing.searchHelperResults = [];
                        }

                    });
                } else if (this.editing.searchHelperResults.length !== 0) {
                    this.editing.searchHelperResults = [];
                }
            },

            onSearchResultChoose(item) {
                this.editing.searchRequest = item;
                this.editing.searchHelperResults = [];
            },

            onCategoryChoose(payload) {
                this.editing.category = payload.category;
                this.editing.parentCategory = payload.parentCategory;
                this.editing.searchHelperResults = [];
            },

            onAddButtonClick() {
                this.$refs.dialog.show();
                setTimeout(() => {
                    this.$refs.searchInput.focus();
                }, 500);
            },

            onDialogReturn() {
                this.editing.category = '';
                this.editing.parentCategory = '';
                this.editing.searchRequest = '';
            },

            onDialogHide() {
                this.editing.category = '';
            },

            onRemove(index) {
                if (this.waybill.products.length !== 1) {
                    this.waybill.products.splice(index, 1);
                }
            },

            onQuantityChange(payload) {
                let {item, newQuantity} = payload;
                item.boughtQuantity = newQuantity;
            }
        },

        computed: {
            ...mapGetters(modules.waybills, [
                'isLoading'
            ]),

            calculatedTotalPrice: function() {
                let res = 0;
                this.waybill.products.forEach((item) => {
                    res += item.boughtQuantity * item.price;
                });
                return Math.round(res * 1000) / 1000;
            },

            id: function () {
                if (this.$route && this.$route.params) {
                    return this.$route.params.id;
                } else {
                    return null;
                }
            },

            hasId: function () {
                return this.id !== null
            },

            hasWaybill: function () {
                return this.waybill !== null
            },
        },

        mounted() {
            if (this.hasId) {
                this.loadWaybill();
            }
        }
    }
</script>

<style lang="sass" scoped>

    .center
        display: flex
        justify-content: center
        height: 100px

    .error
        text-align: center
        font-family: Roboto, sans-serif
        font-size: 1.5rem

    .layout-waybill
        width: 90%
        max-width: 600px
        margin: auto
        font-family: Roboto, sans-serif
        box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.22)
        padding: 25px

        .header
            margin-bottom: 2rem

            p
                text-align: center

            .edit-wrapper
                display: flex
                justify-content: center

                .icon
                    width: 16px
                    height: 16px
                    margin-left: 10px
                    cursor: pointer
                    font-weight: normal

                span
                    font-weight: bold
                    font-size: 14px
                    color: #1d5db5
                    cursor: pointer
                    text-align: center
                    margin-left: auto
                    margin-right: auto
                    padding: 5px
                    border-radius: 5px
                    transition: 0.35s

                    &:hover
                        background-color: #1d5db524

            .title
                font-weight: bold
                font-size: 18px

            .delivery-date
                color: #777777
                font-size: 15px

        .content

            table
                width: 100%
                border-collapse: collapse
                border-top: 2px solid #d0d0d0

                tr
                    &:nth-child(even)
                        background-color: #f2f2f2

                th, td
                    transition: 0.5s
                    text-align: left
                    padding: 16px 8px

            button
                margin-top: 10px
                margin-left: auto
                margin-right: auto

            .comment
                margin-top: 2rem
                margin-bottom: 0

            .comment-min
                margin-top: 1rem
                margin-bottom: 0

            .sub-items
                margin-top: 0.25rem
                display: flex

                .bold
                    font-weight: bold

                .total-price
                    margin-left: auto

                .is-paid-true
                    color: #81C784

                .is-paid-false
                    color: #e57373

    .dialog

        .search-wrapper
            border-bottom: 2px solid #1d5db5

            .search
                display: flex
                position: relative
                justify-content: center
                width: 90%
                margin: auto
                max-width: 500px
                font-family: Roboto, sans-serif

                input
                    display: block
                    width: 100%
                    height: 100%
                    border: none
                    font-size: 1.3rem
                    padding-bottom: 10px
                    padding-top: 10px
                    outline: none

                .search-helper
                    position: absolute
                    top: 43px
                    left: auto
                    right: auto
                    width: 100%
                    box-shadow: 0 20px 20px 0 #0000003d
                    border-bottom-left-radius: 5px
                    border-bottom-right-radius: 5px
                    background-color: #FFFFFF
                    transition: 0.25s

                    p
                        font-size: 18px
                        padding-left: 10px
                        margin: 0
                        padding-top: 1rem
                        padding-bottom: 1rem

                        &:hover
                            background-color: #f2f2f2
                            cursor: pointer
</style>